import React, { Component } from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  Grid,
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";
import PersonIcon from "@material-ui/icons/Person";
import PhoneIcon from "@material-ui/icons/Phone";
import CodeIcon from "@material-ui/icons/Code";

class ApplicationBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isDrawerOpen: false,
      appBarTransparent: true,
      topLeftIconIsMenu: true,
    };
  }

  toggleDrawer() {
    var oldState = this.state;
    this.setState({
      isDrawerOpen: !oldState.isDrawerOpen,
      appBarTransparent: !oldState.appBarTransparent,
      topLeftIconIsMenu: !oldState.topLeftIconIsMenu,
    });
  }

  menuItems = {
    home: { icon: <PersonIcon />, displayName: "Profile" },
    contactMe: { icon: <PhoneIcon />, displayName: "Contact Me" },
  };

  render() {
    return (
      <div>
        <AppBar
          color={this.state.appBarTransparent ? "transparent" : "#ffffff"}
          position="fixed"
          elevation={0}
        >
          <Grid container direction="column">
            <Grid item>
              <Toolbar>
                <CodeIcon />
                <Typography style={{ flexGrow: 1 }} />
                {this.state.topLeftIconIsMenu ? (
                  <MenuIcon
                    onClick={() => this.toggleDrawer()}
                    style={{ cursor: "pointer" }}
                  />
                ) : (
                  <CloseIcon
                    onClick={() => this.toggleDrawer()}
                    style={{ cursor: "pointer" }}
                  />
                )}
              </Toolbar>
            </Grid>
            <Grid item container alignItems="center" jsutify="center">
              {this.state.isDrawerOpen && (
                <div
                  style={{
                    width: "100vw",
                    height: "100vh",
                    backgroundColor: "#ffffff",
                  }}
                >
                  <List style={{ textAlign: "center" }}>
                    {Object.keys(this.menuItems).map((key) => {
                      return (
                        <ListItem
                          button
                          onClick={() => {
                            this.props.changeDisplayPage(key);
                            this.toggleDrawer();
                          }}
                        >
                          <ListItemIcon>
                            {this.menuItems[key]["icon"]}
                          </ListItemIcon>
                          <ListItemText
                            primary={this.menuItems[key]["displayName"]}
                          />
                        </ListItem>
                      );
                    })}
                  </List>
                </div>
              )}
            </Grid>
          </Grid>
        </AppBar>
      </div>
    );
  }
}

export default ApplicationBar;
